import React, { useEffect, useRef, useState } from "react";
import logo3D from "../../Assets/logo-3d-03.png";
import Button from "../../Components/Button/Button";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import gsap from "gsap";
import { useGSAP } from "@gsap/react";

gsap.registerPlugin(useGSAP, ScrollTrigger);


const AboutSection = () => {

  const main = useRef();

  useGSAP(
    () => {
      
    },
    { scope: main }
  );

  const [height, setHeight] = useState(window.innerHeight);
  const elementRef = useRef(null);
  const elements = useRef([]);




  useEffect(() => {
    // const words = gsap.utils.toArray('.box');
    //   words.forEach((word) => {
    //     gsap.to(word, {
    //       color:'#0000',
    //       scrollTrigger: {
    //         trigger: word,
    //        start:"top top",
    //     end:`+=${height*2}`,
    //         scrub: true,
    //         // markers: true,
    //       },
    //     });
    //   });

    gsap.utils.toArray(elements.current).forEach((element, index) => {
      gsap.to(element, {
        // color:'#0000',
        scrollTrigger: {
          trigger: element,
          toggleClass: `color-dark`,
          start:"top top",
          end:`+=${height}`,
        },
        // duration: 9,

      });
    });
    
    

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: elementRef.current,
        pin: true,
        start:"-5% top",
        end:`+=${height}`,
        scrub: true,
        // markers: true,
      },
    });

    return () => {
      tl.kill();
    };
  }, [elementRef]);

  return (
    <div  id="id" className={`h-screen-2 class creative-bg `}>
      <div ref={elementRef} className="trigger h-screen container  px-8 md:px-44 py-8 md:py-16 bg-gradient-to-t from-[#111] to-[30%] to-[#0000] grid place-items-center ">
        <div className="grid md:grid-cols-12">
          <div className="grid gap-2 md:gap-4 col-span-7">
            <p className="text-4xl md:text-5xl md:font-bold  md:leading-tight bg-clip-text bg-gradient-to-r from-[#f6891f] to-rose-600 text-transparent ">
              Lorem ipsum dolor sit, amet 
              <div className="box flex flex-wrap gap-x-4 text-[#fff]" ref={main}>
                <p ref={(el) => (elements.current[0] = el)} className="" >consectetur</p>
                <p ref={(el) => (elements.current[2] = el)} className="" >adipisicing</p>
                <p ref={(el) => (elements.current[4] = el)} className="" >elit.</p>
                <p ref={(el) => (elements.current[6] = el)} className="" >Pariatur,</p>
                <p ref={(el) => (elements.current[8] = el)} className="" >sint</p>
                <p ref={(el) => (elements.current[10] = el)} className="" >ad</p>
                <p ref={(el) => (elements.current[12] = el)} className="" >nulla</p>
                <p ref={(el) => (elements.current[14] = el)} className="" >eveniet</p>
                <p ref={(el) => (elements.current[16] = el)} className="" >doloribus</p>
                <p ref={(el) => (elements.current[18] = el)} className="" >necessitatibus.</p>
                <p ref={(el) => (elements.current[20] = el)} className="" >consectetur</p>
                <p ref={(el) => (elements.current[22] = el)} className="" >adipisicing</p>
                <p ref={(el) => (elements.current[24] = el)} className="" >elit.</p>
                <p ref={(el) => (elements.current[26] = el)} className="" >Pariatur.</p>

              </div>
            </p>
            <p className="text-sm font-thin">
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Molestiae
              asperiores laborum magnam mollitia quibusdam odit dolores
              laboriosam natus? Pariatur, dolorum?
            </p>
            <div className="">
              <Button name="more" />
            </div>
          </div>
          <div className="col-span-5 grid place-items-center py-4 px-16 md:px-0">
            <img className="w-full" src={logo3D} alt="" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutSection;
